@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

body {
  width: 100%;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  margin: 0; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually_hidden:not(:active),
input[type="checkbox"].visually_hidden,
input[type="radio"].visually_hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center; }

.page {
  background-color: #ffffff; }

.header {
  width: 100%;
  padding-top: 23px; }

.header__wrapper {
  width: 62.6vw; }
  @media (max-width: 1905px) {
    .header__wrapper {
      width: 95.6vw;
      min-width: 320px; } }

.header__first-level,
.header__second-level {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center; }

.header__second-level {
  margin: 24px 0 0 0; }

.main-nav {
