      justify-content: space-between; } }

.main-nav__place {
  font-size: 16px;
  line-height: 21px;
  font-weight: 700;
  text-align: center;
  width: 5.9vw;
  color: #4a4a4a;
  font-family: "PT Sans", "Arial", sans-serif;
  border: none;
  border-bottom: 1px dotted #4a4a4a;
  outline: none; }
  @media (max-width: 1905px) {
    .main-nav__place {
      flex-grow: 0.5; } }
  @media (max-width: 1150px) {
    .main-nav__place {
      flex-grow: 1; } }

.main-nav__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 15.05vw;
  list-style: none;
  padding: 0;
  margin: 0 0 3px 4.9vw; }
  @media (max-width: 1905px) {
