  border: 2px solid #FFCC00;
  border-radius: 100px;
  outline: none;
  background-color: #ffffff; }
  .search__input::-webkit-input-placeholder {
    font-family: "PT Sans", "Arial", sans-serif;
    font-size: 16px;
    color: #C4C4C4;
    font-style: normal;
    background: #ffffff;
    padding: 0 0 0 8px; }
  .search__input:-moz-placeholder {
    font-family: "PT Sans", "Arial", sans-serif;
    font-size: 16px;
    color: #C4C4C4;
    font-style: normal;
    background: #ffffff;
    padding: 0 0 0 8px; }
  .search__input:-ms-input-placeholder {
    font-family: "PT Sans", "Arial", sans-serif;
    font-size: 16px;
    color: #C4C4C4;
    font-style: normal;
    background: #ffffff;
    padding: 0 0 0 8px; }

.search__button {
  position: absolute;
  top: 0;
  right: 0;
  width: 44px;
  height: 45px;
  border: none;
  background: transparent;
  cursor: pointer;
  background-image: url("../img/icon-search.svg");
  background-repeat: no-repeat;
  background-position: 0px 4px; }

.user {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 12.29vw; }
  @media (max-width: 1905px) {
