  color: #4a4a4a; }

.user__signature {
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  font-size: 13px;
  line-height: 10px;
  font-weight: 400;
  text-align: center;
  margin: 0 2px 8px 0;
  color: #8F8F8F; }

.user__basket {
