  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  min-width: 320px;
  background-color: rgba(255, 255, 255, 0.9); }

.popup__wrapper {
