.popup__city-item {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: start;
  margin: 0 0 16px 0; }

.popup__city {
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  text-align: left; }
  .popup__city-main {
